.container {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  width: fit-content;
  scale: 1.2;
  margin-left: 1rem;
  position: absolute;
  top: 85vh;
  z-index: 1000;
}

.container a {
  transition: .3s;
}

.container a:hover {
  transform: scale(1.2);
}

@media (max-width: 720px) {
  .container {
    top: 80vh;
  }
}